export default {
  name: 'PressCard',
  props: {
    title: {
      type: String,
      default: '',
    },
    subTitle: {
      type: String,
      default: '',
    },
    extra: {
      type: String,
      default: '',
    },
    thumbnail: {
      type: String,
      default: '',
    },
    cover: {
      type: String,
      default: '',
    },
    isFull: {
      // 内容区域是否通栏
      type: Boolean,
      default: false,
    },
    isShadow: {
      // 是否开启阴影
      type: Boolean,
      default: true,
    },
    shadow: {
      type: String,
      default: '0px 0px 3px 1px rgba(0, 0, 0, 0.08)',
    },
    border: {
      type: Boolean,
      default: true,
    },
    margin: {
      type: String,
      default: '15px',
    },
    spacing: {
      type: String,
      default: '0 10px',
    },
    padding: {
      type: String,
      default: '10px',
    },
  },
  emits: ['click'],
  methods: {
    onClick(type) {
      this.$emit('click', type);
    },
  },
};
